import { Container, Grid, Typography } from "@mui/material";
import SingleImage from "../SingleImage/SingleImage";

import appsealingLogo from "../../images/appsealingLogo.png";
import bharatClaimsLogo from "../../images/bharatClaimsLogo.png";
import bookMyJetLogo from "../../images/bookMyJetLogo2.png";
import appsealingOwner from "../../images/appsealingOwner.jpg";
import bharatClaimsOwner from "../../images/bharatClaimsOwner.JPG";
import bookMyJetOwner from "../../images/bookMyJetOwner.jpg";

const users = [
  {
    name: "Govindraj Basatwar",
    company: "Appsealing",
    CompanyLogo: appsealingLogo,
    CompanyOwner: appsealingOwner,
  },
  {
    name: "Himanshu Gulati",
    company: "Bharat Claims",
    CompanyLogo: bharatClaimsLogo,
    CompanyOwner: bharatClaimsOwner,
  },
  {
    name: "Santosh Sharma",
    company: "Book My Jet",
    CompanyLogo: bookMyJetLogo,
    CompanyOwner: bookMyJetOwner,
  },
];
const Sponsors = (props) => {
  return (
    <Container
      maxWidth="90%"
      sx={{
        textAlign: "center",
      }}
    >
      <Typography mt={6} mb={6} variant="h2" fontWeight={500}>
        Sponsors
      </Typography>
      <Typography variant="h5" mb={6} fontWeight={100}>
        Where Investor meets Opportunities and Startup meet new beginnings.
      </Typography>
      <Grid
        sx={{
          display: "flex",
          justifyContent: "space-evenly",
          alignItems: "center",
        }}
        spacing={1}
        container
        direction={"row"}
        mb={4}
      >
        {users.map((user) => (
          <Grid sx={{ padding: "0" }} item>
            <SingleImage
              logo={user.CompanyLogo}
              image={user.CompanyOwner}
              key={Math.floor(Math.random() * 1000)}
              name={user.name}
              company={user.company}
            />
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default Sponsors;
