import { Typography, Button } from "@mui/material";
import { Link } from "react-router-dom";

import classes from "./Countdown.module.css";

import React, { useState, useRef, useEffect } from "react";
const Countdown = (props) => {
  const Ref = useRef(null);

  const [timer, setTimer] = useState("00:00:00");

  const getTimeRemaining = (e) => {
    const total = Date.parse(e) - Date.parse(new Date());
    const seconds = Math.floor((total / 1000) % 60);
    const minutes = Math.floor((total / 1000 / 60) % 60);
    const hours = Math.floor((total / 1000 / 60 / 60) % 24);
    return {
      total,
      hours,
      minutes,
      seconds,
    };
  };

  const startTimer = (e) => {
    let { total, hours, minutes, seconds } = getTimeRemaining(e);
    if (total > 0) {
      setTimer(
        (hours > 9 ? hours : "0" + hours) +
          ":" +
          (minutes > 9 ? minutes : "0" + minutes) +
          ":" +
          (seconds > 9 ? seconds : "0" + seconds)
      );
    }
    else if(total == 0)
    {
        setTimer('expired')
    }
  };

  const clearTimer = (e) => {
    setTimer("-");
    if (Ref.current) clearInterval(Ref.current);
    const id = setInterval(() => {
      startTimer(e);
    }, 1000);
    Ref.current = id;
  };

  const getDeadTime = () => {
    let deadline = new Date();
    deadline.setSeconds(deadline.getSeconds() + 1000);
    return deadline;
  };
  useEffect(() => {
    clearTimer(getDeadTime());
  }, []);
  return (
    <div className={classes.remainingTime__div}>
      <div className={classes.remainingTime__content}>
        <h4>
          <span>Don't miss out. </span>Event Starting in :
        </h4>
        <Typography color="#707375">{timer}</Typography>
        <Link to={"/checkout"}>
          <Button size="large" variant="outlined">
            BUY TICKET
          </Button>
        </Link>
      </div>
    </div>
  );
};
export default Countdown;
