import { Typography } from "@mui/material";
import classes from "./About.module.css";
import aboutImg from '../../images/about.png'
const About = (props) => {
  return (
    <div className={classes.event__div}>
      <div className={classes.grid}>
        <div className={classes.grid_item}>
          <img
            width={"100%"}
            className={classes.about__img}
            src={aboutImg}
            alt="img"
          ></img>
        </div>
        <div className={classes.grid_item}>
          <Typography mt={7} color="green" variant="h4" fontFamily={"cursive"}>
            About
          </Typography>
          <Typography color={"black"} variant="h3">
            Why join Startup Story
          </Typography>
          <Typography mt={2} fontWeight={200} color={"black"} variant="body1">
            Sapio Analytics presents 21 By 72 is an event by IVY Growth
            Associates is a platform that caters to exposure, networking,
            collaboration, and investments all under one roof. That makes it
            worth attending!
          </Typography>
        </div>
      </div>
    </div>
  );
};

export default About;
