import { Button, Grid, Link, Stack, Typography } from "@mui/material";
import classes from "./Footer.module.css";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import InstagramIcon from "@mui/icons-material/Instagram";
import Logo from "../../images/logo2.png";

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: "transparent",
  ...theme.typography.body2,
  padding: theme.spacing(10),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

const Footer = (props) => {
  return (
    <Box
      sx={{
        marginTop: "5rem",
        flexGrow: 1,
        backgroundColor: "black",
        width: "100%",
      }}
    >
      <Grid container spacing={2}>
        <Grid item xs={12} md={4}>
          <Item>
            <Stack spacing={4}>
              <img src={Logo} alt={"logo"} />
              <Typography color={"#969696"}>
                Innovators and Investors get ready to transform your vision into
                a mission.
              </Typography>
              <div className={classes.social__links}>
                <FacebookIcon
                  sx={{
                    color: "#969696",
                    cursor: "pointer",
                    fontSize: "3rem",
                    ":hover": { color: "white" },
                    transition: "all 0.1s",
                  }}
                />
                <TwitterIcon
                  sx={{
                    color: "#969696",
                    cursor: "pointer",
                    fontSize: "3rem",
                    ":hover": { color: "white" },
                    transition: "all 0.1s",
                  }}
                />
                <LinkedInIcon
                  sx={{
                    color: "#969696",
                    cursor: "pointer",
                    fontSize: "3rem",
                    ":hover": { color: "white" },
                    transition: "all 0.1s",
                  }}
                />
                <InstagramIcon
                  sx={{
                    color: "#969696",
                    cursor: "pointer",
                    fontSize: "3rem",
                    ":hover": { color: "white" },
                    transition: "all 0.1s",
                  }}
                />
              </div>
            </Stack>
          </Item>
        </Grid>
        <Grid item xs={12} md={4}>
          <Item>
            <Stack>
              <Typography variant="h5" mb={3} mt={3} color={"white"}>
                USEFUL LINKS
              </Typography>
            </Stack>
            <Stack>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Book a Startup Stall
                </Typography>
              </Link>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Book Your Stay
                </Typography>
              </Link>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Grab Your Visitors Pass
                </Typography>
              </Link>
            </Stack>
            <Stack>
              <Typography variant="h5" mb={3} mt={3} color={"white"}>
                IT MAY INTEREST YOU
              </Typography>
            </Stack>
            <Stack>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Privacy Policy
                </Typography>
              </Link>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Refunds and Cancellations
                </Typography>
              </Link>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Contact Us
                </Typography>
              </Link>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  Terms and Conditions
                </Typography>
              </Link>
              <Link>
                <Typography sx={{ cursor: "pointer" }} color={"#969696"}>
                  About Us
                </Typography>
              </Link>
            </Stack>
          </Item>
        </Grid>
        <Grid item xs={12} md={4}>
          <Item>
            <Typography variant="h6" mb={3} mt={3} color={"white"}>
              SUBSCRIBE to our NEWSLETTER
            </Typography>
            <Grid container direction="column" spacing={2}>
              <Grid item xs={3}>
                <input
                  className={classes.newsletter__input}
                  placeholder="Name"
                  type={"text"}
                  required
                />
              </Grid>
              <Grid item xs={3}>
                <input
                  className={classes.newsletter__input}
                  placeholder="Email"
                  type={"email"}
                  required
                />
              </Grid>
              <Grid item xs={3}>
                <button className={classes.newsletter__button}>
                  Subscribe
                </button>
              </Grid>
            </Grid>
          </Item>
        </Grid>
        <hr className={classes.hr} />
        <div className={classes.lastline}>
          <Typography
            mb={9}
            sx={{ textAlign: "center" }}
            color={"white"}
            variant="body1"
          >
            © 2022, Startup Story Private Limited. All rights reserved
          </Typography>
        </div>
      </Grid>
    </Box>
  );
};

export default Footer;
