export default function OppositeContentTimeline() {
  const [eventData, setEventData] = React.useState(eventDataDay1);
  const day1Handler = () => {
    setEventData(eventDataDay1);
  };
  const day2Handler = () => {
    setEventData(eventDataDay2);
  };
  const day3Handler = () => {
    setEventData(eventDataDay3);
  };
  return (
    <div className={classes.timeline__container}>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Typography color="green" variant="h4" fontFamily={"cursive"}>
          Schedule
        </Typography>
        <Typography
          textAlign={"center"}
          color={"white"}
          mb={3}
          variant="h2"
          fontWeight={500}
        >
          Information of Event Schedules
        </Typography>
        <Typography
          textAlign={"center"}
          color={"white"}
          variant="h5"
          fontWeight={100}
        >
          A source to remind your favorite event. Don't miss it out.
        </Typography>
        <div className={classes.timeline__buttons__div}>
          <button onClick={day1Handler} className={classes.timeline__button}>
            Day 1
          </button>
          <button onClick={day2Handler} className={classes.timeline__button}>
            Day 2
          </button>
          <button onClick={day3Handler} className={classes.timeline__button}>
            Day 3
          </button>
        </div>
      </Box>
      <div className={classes.timeline__tree__container}>
        <Timeline sx={{maxWidth:'100%'}} position="alternate">
          {eventData.map((event) => (
            <TimelineItem> 
              <TimelineOppositeContent color="white">
                {event.time}
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot color="primary" />
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <EventAccordion
                  heading={event.heading}
                  description={event.description}
                />
              </TimelineContent>
            </TimelineItem>
          ))}
        </Timeline>
      </div>
    </div>
  );
}
