import { Box, Button, Typography } from "@mui/material";
import React from "react";
import classes from "./PassCard.module.css";
const PassCard = (props) => {
  return (
    <Box
      sx={{
        backgroundColor: `${props.bgcolor}`,
        color: `${props.color}`,
        width: "20rem",
        height: "35rem",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        textAlign: "center",
        borderRadius: "10px",
      }}
    >
      <div>
        <br />
        <Typography fontWeight={700} variant="h5">
          {props.heading}
        </Typography>
        <br />
        <Typography variant="h2" fontWeight={900}>
          ₹ {props.price}
        </Typography>
        <br />
        <br />
        <br />
        {props.benefits.map((item) => (
          <div className={classes.benefits}>
            <React.Fragment>
              <Typography
                sx={{ width: "70%", textAlign: "left" }}
                fontWeight={200}
                variant="body1"
              >
                ✔ {item}
              </Typography>
              <br></br>
            </React.Fragment>
          </div>
        ))}
      </div>
      <div>
        <Button variant="outlined">{props.buttontext}</Button>
        <br />
        <br />
        <br />
      </div>
    </Box>
  );
};

export default PassCard;
