import { Link, Stack, Typography } from "@mui/material";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import classes from "./SingleKeynoteSpeaker.module.css";

const SingleKeynoteSpeaker = (props) => {
  return (
    <div className={classes.container}>
      <div className={classes.featured__img}>
        <div className={classes.cover}>
          <Typography sx={{opacity:'1'}} color={'white'} variant="h5" fontFamily={"monospace"} fontWeight={800}>
            {props.name}
          </Typography>
          <Typography fontFamily={"monospace"} variant="h6">
          {/* Former CEO | Starlink India & Founding Team Member | Paypal */}
          {props.description}
          </Typography>
          <Stack ml={4} direction={"row"}>
            <Link href={``}>
              <LinkedInIcon
                sx={{
                  color: "white",
                  cursor: "pointer",
                  fontSize: "3rem",
                  ":hover": { color: "#0a66c2" },
                  transition: "all 0.1s",
                }}
              />
            </Link>
          </Stack>
        </div>
        <img alt="keynote Speakers" src={props.src} />
      </div>
    </div>
  );
};

export default SingleKeynoteSpeaker;
