import { Container, Grid, Typography } from "@mui/material";
import PassCard from "./PassCard";

const Pass = (props) => {
  return (
    <Container
      sx={{
        textAlign: "center",
      }}
    >
      <Typography mt={7} color="green" variant="h4" fontFamily={"cursive"}>
        Get Your Pass
      </Typography>
      <Typography mb={6} variant="h2" fontWeight={500}>
        You Don't want to miss this out
      </Typography>
      <Typography variant="h5" mb={6} fontWeight={100}>
        Fortune favors the bold. Hurry Up!
      </Typography>
      <Grid
        sx={{
          display: "flex",
          justifyContent: "space-evenly",
          alignItems: "center",
        }}
        spacing={3}
        container
        direction={"row"}
        mt={12}
        mb={12}
      >
        <Grid item>
          <PassCard
            bgcolor="black"
            color="white"
            heading="Startup Full Pass"
            price="35000"
            buttontext="Book Your Stall"
            benefits={[
              "3 Days Event",
              "Access to Premium Stall",
              "2 Days Stall at Exhibition",
            ]}
          />
        </Grid>
        <Grid item>
          <PassCard
            bgcolor="#f6f9ff"
            color="black"
            heading="Investors Full Pass"
            price="2500"
            buttontext="Book Your Visit"
            benefits={[
              "3 Days Event",
              "Access to Startup Pitches",
              "Access to Startup Exhibition",
              "Access to Panel Discussions",
            ]}
          />
        </Grid>
        <Grid item>
          <PassCard
            bgcolor="#eeffff"
            color="black"
            heading="Stay Access"
            price="25000"
            buttontext="Book Your Stay"
            benefits={[
              "3 Days Event",
              "Double Accommodation",
              "Food Included",
              "Easy Networking",
            ]}
          />
        </Grid>
      </Grid>
    </Container>
  );
};
export default Pass;
