import { Container, Grid, Typography } from "@mui/material";
import SingleImage from "../SingleImage/SingleImage";

import CompanyLogo from '../../images/dogtailLogo.png'
import CompanyOwner from '../../images/dogTail.jpg'

const users = [
  {
    name: "Vikram Singh",
    company : 'Dogtail Films',
    CompanyLogo,
    CompanyOwner
  },
  
];
const VideoProductionPartner = (props) => {
  return (
    <Container
      maxWidth="90%"
      sx={{
        textAlign: "center",
      }}
    >
      <Typography mt={6} mb={6} variant="h2" fontWeight={500}>
        Video Production Partner
      </Typography>
      <Typography variant="h5" mb={6} fontWeight={100}>
        Where Investor meets Opportunities and Startup meet new beginnings.
      </Typography>
      <Grid
        sx={{
          display: "flex",
          justifyContent: "space-evenly",
          alignItems: "center",
        }}
        spacing={1}
        container
        direction={"row"}
        mb={4}
      >
        {users.map((user) => (
          <Grid item>
            <SingleImage
              logo = {user.CompanyLogo}
              image = {user.CompanyOwner}
              key={Math.floor(Math.random() * 1000)}
              name={user.name}
              company={user.company}
            />
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default VideoProductionPartner;
