import { Grid, Stack, Typography } from "@mui/material";
import { useState, useEffect } from "react";

const Stats = (props) => {
  let [count1, setCount1] = useState(0);
  let [count2, setCount2] = useState(0);
  let [count3, setCount3] = useState(0);
  let [count4, setCount4] = useState(0);
  useEffect(() => {
    setTimeout(() => {
      if (count1 !== 100) count1++;
      if (count2 !== 500) count2++;
      if (count3 !== 50) count3++;
      if (count4 !== 10) count4++;
      setCount1(count1);
      setCount2(count2);
      setCount3(count3);
      setCount4(count4);
    }, 10);
  }, [count1, count2, count3, count4]);

  return (
    <Grid
      sx={{
        display: "flex",
        justifyContent: "space-evenly",
        alignItems: "center",
        marginTop:'4rem',
      }}
      container
    >
      <Grid xs={6} md={3} item>
        <Stack sx={{ textAlign: "center" }}>
          <Typography fontWeight={600} variant="h2">
            {count1} +
          </Typography>
          <Typography fontWeight={600} color={"#888888"} variant="body1">
            Startup Exhibitors
          </Typography>
        </Stack>
      </Grid>
      <Grid xs={6} md={3} item>
        <Stack sx={{ textAlign: "center" }}>
          <Typography fontWeight={600} variant="h2">
            {count2} +
          </Typography>
          <Typography fontWeight={600} color={"#888888"} variant="body1">
            Investors
          </Typography>
        </Stack>
      </Grid>
      <Grid xs={6} md={3} item>
        <Stack sx={{ textAlign: "center" }}>
          <Typography fontWeight={600} variant="h2">
            {count3} +
          </Typography>
          <Typography fontWeight={600} color={"#888888"} variant="body1">
            Angel Networks
          </Typography>
        </Stack>
      </Grid>
      <Grid xs={6} md={3} item>
        <Stack sx={{ textAlign: "center" }}>
          <Typography fontWeight={600} variant="h2">
            {count4} +
          </Typography>
          <Typography fontWeight={600} color={"#888888"} variant="body1">
            Venture Capitalist
          </Typography>
        </Stack>
      </Grid>
    </Grid>
  );
};
export default Stats;
